.section-one {
  background-color: var(--bg);
  height: 90vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.section-one img {
  max-width: 350px;
  height: auto;
  border-radius: 40% 60% 30% 70% / 57% 22% 78% 43%;
  border: solid 16px var(--bg);
  box-shadow: 12px 12px 40px #5f5f5f75;
  z-index: 1;
  animation: slideUp 500ms ease-out;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
  transform: translateY(200px);
}
@keyframes slideUp {
  0%{
    transform: translateY(200px);
  }
  100%{
    transform: translateY(0px);
  }
}
.section-one-wrapper {
  text-align: left;
  z-index: 1;
  overflow: hidden;
  animation: slideUp 500ms ease-out;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
  transform: translateX(-200px);
}
.section-one-wrapper h1 {
  animation: slideInTop 1.5s ease-out;
  animation-delay: 1.3s;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
}
@keyframes slideInTop {
  0%{
    transform: translateY(-100px);
  }
  100%{
    transform: translateY(0px);
  }
}
.section-one-circle {
  position: absolute;
  top: -25vw;
  right: 0vw;
  background-color: #6d6d6d20;
  border-radius: 50%;
  height: 800px;
  width: 800px;
  filter: blur(80px);
  z-index: 0;
}
.section-one-button-wrapper {
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 20px 0 0 0;
  gap: 20px;
}
.section-one-button-wrapper a {
  border-radius: 32px;
}
.section-one-button {
  width: 120px;
  height: 40px;
  border-radius: 32px;
  box-shadow: 2px 2px 10px #6d6d6d20;
  background-color: var(--bg);
  transition: 100ms ease-in;
  cursor: pointer;
  border: solid 1px var(--border);
}
.section-one-button:hover {
  background-color: var(--sg);
  transition: 50ms;
  color: var(--bg);
  border: solid 2px var(--sg);
}
.scroll {
  position: absolute;
  display: flex;
  bottom: 100px;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 15px;
  animation: pulse 5s infinite;
  opacity: 30%;
}
@keyframes pulse {
  0%{
    opacity: 30%;
  }
  50%{
    opacity: 100%;
  }
  100% {
    opacity: 30%;
  }
}

/*Half Screen*/
@media (max-width: 1000px) {
  .section-one img {
    max-width: 250px;
    border: solid 8px var(--bg);
  }
}
/*Mobile*/
@media (max-width: 480px) {
  .section-one {
    flex-direction: column-reverse;
  }
  .section-one img {
    max-width: 200px;
    border: solid 8px var(--bg);
  }
  .section-one-wrapper {
    text-align: center;
  }
  .scroll {
    bottom: 80px;
    animation: pulse 5s infinite;
    animation-delay: 3s;
    opacity: 50%;
  }
}