.loader {
  position: absolute;
  display: flex;
  flex-direction: column;
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: var(--sg);
  justify-content: center;
  align-items: center;
  animation: slideOut 500ms ease-in;
  animation-fill-mode: forwards;
  animation-delay: 1.4s;
}

.loader div {
  overflow: hidden;
}

.loader h1 {
  transform: translateY(200px);
  animation: slideIn 1.5s ease;
  animation-fill-mode: forwards;
  color: var(--bg);
  margin: 0;
  padding: 0;
}

.loader hr {
  background-color: var(--bg);
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  width: 10px;
  height: 4px;
  border-radius: 12px;
  animation: expand .5s ease-in;
  animation-fill-mode: forwards;
}

@keyframes expand {
  0% {
    width: 1px;
  }

  100% {
    width: 160px;
  }
}

@keyframes slideIn {
  0% {
    opacity: 0%;
  }

  100% {
    transform: translateY(0px);
    opacity: 100%;
  }
}

@keyframes slideOut {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100vh);
    pointer-events: none;
  }
}

/*Mobile*/
@media (max-width: 480px) {
  .loader hr {
    height: 2px;
  }
  @keyframes expand {
    0% {
      width: 1px;
    }

    100% {
      width: 80px;
    }
  }
}