.section-three {
    background-color: var(--bg2);
    height: fit-content;
    display: flex;
    flex-direction: column;
    padding: 0 12vw 100px 12vw;
    gap: 20px;
  }
.section-three-title {
    overflow: hidden;
}
.section-three-title-h2 {
    transform: translateY(50px);
}
.section-three-skills {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    overflow: hidden;
    padding: 0 3vw 0 3vw;
    gap: 20px;
}