/*Root*/
@import url('https://fonts.googleapis.com/css2?family=Quicksand&family=Urbanist:wght@800&display=swap');
:root {
  --bg: #ffffff;
  --bg2: #f6f6f6;
  --text: #353535;
  --ltext: #494949;
  --sg: #78866b;
  --lsg: #8f9779;
  --border: #bbbbbb;
  --shadow: #bbbbbb20;
  --select: #b8c0a3;
  --p: 18px;
  --a: 18px;
  --h1: 72px;
  --h2: 36px;
  --h3: 18px;
  --h4: 36px;
  --b: 14px;
}
::selection {
  background: var(--lsg);
  color: var(--bg);
}
::-webkit-scrollbar {
  width: 0px;
}
h1 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h1);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h2 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h2);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h3 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h3);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h4 {
  font-family: 'Quicksand', sans-serif;
  font-size: var(--h4);
  color: var(--ltext);
  padding: 0;
  margin: 0;
}
p {
  font-family: 'Quicksand', sans-serif;
  font-size: var(--p);
  color: var(--ltext);
  padding: 0;
  margin: 0;
}
ul, a, li {
  text-decoration: none;
  text-transform: uppercase;
  list-style: none;
  font-family: 'Urbanist', sans-serif;
  font-size: var(--a);
  color: var(--ltext);
  padding: none;
  margin: none;
  list-style-type: none;
  padding-inline-start: 0;
}
button {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--b);
  color: var(--ltext);
  padding: 0;
  margin: 0;
  text-align: center;
  align-items: center;
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  animation: bodyScroll 0s;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}
@keyframes bodyScroll {
  0%{
    overflow: hidden;
  }
  99% {
    overflow: hidden;
  }
  100%{
    overflow-y: scroll;
  }
}

html {
  scroll-behavior: smooth;
}

/*Half Screen*/
@media (max-width: 1000px) {
  :root {
    --h1: 64px;
  }
}
/*Mobile*/
@media (max-width: 480px) {
  :root {
    --p: 12px;
    --a: 12px;
    --h1: 36px;
    --h2: 24px;
    --h3: 12px;
    --h4: 24px;
    --b: 10px;
  }
}