.section-six {
    background-color: var(--bg2);
    height: fit-content;
    display: flex;
    flex-direction: column;
    padding: 0 12vw 100px 12vw;
    gap: 20px;
  }
.section-six-title {
    overflow: hidden;
}
.section-six-title-h2 {
    transform: translateY(50px);
}
.bento-wrapper {
    height: 500px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: center;
}
.box {
    background-color: var(--bg);
    box-shadow: 2px 2px 15px var(--shadow);
}
.box:hover {
    cursor: pointer;
}
#box-one {
    height: 500px;
    width: 500px;
}
.row-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
#box-two {
    height: 240px;
    width: 480px;
}
#box-three {
    height: 240px;
    width: 480px;
}

/*Half Screen*/
@media (max-width: 1000px) {
    .section-six {
        gap: 15px;
      }
    .bento-wrapper {
        gap: 15px;
    }
    #box-one {
        height: 500px;
        width: 250px;
    }
    #box-two {
        height: 240px;
        width: 240px;
    }
    #box-three {
        height: 240px;
        width: 240px;
    }
  }
  /*Mobile*/
  @media (max-width: 480px) {
    .section-six {
        gap: 15px;
      }
    .bento-wrapper {
        gap: 15px;
        flex-direction: column;
        height: fit-content;
    }
    #box-one {
        height: 240px;
        width: 100%;
    }
    #box-two {
        height: 240px;
        width: 100%;
    }
    #box-three {
        height: 240px;
        width: 100%;
    }
    .row-wrapper {
        gap: 15px;
    }
  }