/*Header*/
nav {
    width: 100%;
    position: absolute;
    top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
  }

  nav li {
    padding: 0 10px 0px 30px;
    float: left;
  }
  nav a:after {
    content: '';
    display: block;
    margin: auto;
    height: 2px;
    width: 0px;
    background: transparent;
    transition: width 200ms ease-out, background-color 400ms ease;
  }
  nav a:hover:after {
    width: 80%;
    background: var(--ltext);
  }

    /*Mobile*/
    @media (max-width: 480px) {
      nav {
        width: fit-content;
        position: absolute;
        top: 10px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        z-index: 2;
      }
    }